@-webkit-keyframes mdinc-fade {
  0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(0);
    transform: translate(-50%, -50%) scale(0);
  }
  50% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(.5);
    transform: translate(-50%, -50%) scale(.5);
  }
  to {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(1);
    transform: translate(-50%, -50%) scale(1);
  }
}
@keyframes mdinc-fade {
  0% {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(0);
    transform: translate(-50%, -50%) scale(0);
  }
  50% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) scale(.5);
    transform: translate(-50%, -50%) scale(.5);
  }
  to {
    opacity: 0;
    -webkit-transform: translate(-50%, -50%) scale(1);
    transform: translate(-50%, -50%) scale(1);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
div.loading .mdinc-loaderOuter {
  display: block;
}
.mdinc-loaderOuter {
  top: 0;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(35, 35, 35, .5);
  z-index: 99;
  display: none;
  height: 100%;
  width: 100%;
}
.mdinc-loaderOuter .mdinc-loader {
  border: 2px solid rgba(255, 255, 255, .9);
  border-radius: 50%;
  border-top: 2px solid rgba(255, 255, 255, .2);
  width: 40px;
  height: 40px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
  position: absolute;
  z-index: 999999999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: -20px 0 0 -20px;
}
.mdinc-loaderOuter .mdinc-loader:after,
.mdinc-loaderOuter .mdinc-loader:before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border: 1px solid #fff;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  opacity: 1;
  transition: border 1s ease;
  pointer-events: none;
}
.mdinc-loaderOuter .mdinc-loader:before {
  -webkit-animation: mdinc-fade 4.5s ease -3s infinite;
  animation: mdinc-fade 4.5s ease -3s infinite;
}
.mdinc-loaderOuter .mdinc-loader:after {
  -webkit-animation: mdinc-fade 4.5s ease -1.5s infinite;
  animation: mdinc-fade 4.5s ease -1.5s infinite;
}
